@import url(https://fonts.googleapis.com/css2?family=Inter:wght@500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}


html {
  box-sizing: border-box;
}


/* @import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,600;0,700;0,900;1,600;1,900&display=swap'); */


.App {
  min-height: 100vh;
  background: #F1F7FD;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  padding-left: 2rem;
  padding-right: 2rem;
}




#home {
    width: 100vw;
}

.container {
    width: 100%;
    min-height: 600px;
    max-width: 1500px;
    padding: 0 100px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    background: #F1F7FD;
    margin:auto;
}

#home h1 {
    font-size: 3rem;
    font-family: 'Inter', sans-serif;
    font-weight: 600;
}

#home .left {
    padding: 40px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

#home .right {
    padding: 40px;
    text-align: center;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.white-background {
    width: 100vw;
    background-color: #fff;
}

.intro {
    background-color: #fff;
    max-width: 800px;
    margin: auto;
    padding-top: 6rem;
    padding-bottom: 6rem;
}

.intro video {
    margin-bottom: 4rem;
}

.intro img {
    width: 100%;
    margin-bottom: 2rem;
}

.intro p {
    margin-bottom: 2rem;
}

.intro h2 {
    margin-bottom: 2rem;
    margin-top: 4rem;
    text-align: center;
}

.courses {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    text-align: center;
    justify-content: center;
}

.courses img.blockchain {
    width: 150px;
}

.courses img.constellation {
    width: 125px;
}

.course {
    background-color: #0A2540;
    text-align: center;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    color: #fff;
    padding: 40px;
    border-radius: 10px;
}

.course h3 {
    font-size: 1rem;
    font-family: 'Inter', sans-serif;
    font-weight: 600;
    margin-bottom: 5px;
}
#course-one-home {
    width: 100vw;
    background: #0F1B35;
}

#course-one-home .container-boom {
    width: 100%;
    min-height: 600px;
    max-width: 1300px;
    padding: 0 100px;
    /* display: grid;
    grid-template-columns: 1fr; */
    background: #0F1B35;
    margin:auto;
    text-align: center;
    padding-top: 8rem;
    position: relative;
}

#course-one-home .head {
    width: 100%;
    text-align: center;
}

#course-one-home .container-boom img.curve {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
}

#course-one-home h1 {
    font-size: 1.8rem;
    font-family: 'Inter', sans-serif;
    font-weight: 600;
    color: #fff;
    margin-top: 2rem;
}

#course-one-home .left {
    padding: 40px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

#course-one-home .right {
    padding: 40px;
    text-align: center;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.white-background {
    width: 100vw;
    background-color: #fff;
}

.description {
    background-color: #fff;
    max-width: 800px;
    margin: auto;
    padding-top: 6rem;
}

.description img {
    width: 100%;
    margin-bottom: 2rem;
}

.description p {
    margin-bottom: 2rem;
}

.description h2 {
    margin-bottom: 4rem;
    margin-top: 4rem;
    text-align: center;
}

.description p {
    font-size: 1.2rem;
    font-family: 'Inter', sans-serif;
    font-weight: 400;
}

.description p span {
    color: #41C2D2;
}

.description img.dropdown {
    margin-bottom: 4rem;
}

.white-background-two {
    width: 100vw;
    background-color: rgb(255, 255, 255);
    text-align: center;
}

.description-two {
    background-color: rgb(255, 255, 255);
    max-width: 1500px;
    margin: auto;
    padding-top: 2rem;
    padding-bottom: 4rem;
}

.description-two h2 {
    margin-bottom: 4rem;
    margin-top: 4rem;
    text-align: center;
}

.footer-background {
    width: 100vw;
    background-color: #F1F7FD;
}

.footer-container {
    width: 100%;
    /* min-height: 200px; */
    max-width: 800px;
    /* padding: 0 100px; */
    margin:auto;
    text-align: center;
}

.form-field {
    display: grid;
    grid-template-columns: 2fr 2fr 1fr;
    grid-gap: 1rem;
    padding-bottom: 4rem;
}

.three {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    background-color: #fff;
    padding: 15px 10px;
    box-shadow: 0px -4px 0px #CFCFCF;
    border-radius: 5px;
    font-size: .8rem;
    color: #555555;
}

#footer button {
    padding: 12px 30px;
    background-color: #41C2D2;
    color: #fff;
    border: none;
    box-shadow: 0px 4px 0px #31919D;
    border-radius: 8px;
    font-size: 1rem;
    font-weight: 600;
    font-family: 'Inter', sans-serif;
}

#footer h2 {
    margin: 2rem 0rem;
}

.nav-container {
    padding: 0 100px;
    width: 100%;
    height: 100%;
    min-height: 110px;
    max-width: 1500px;
    display: flex;
    align-items: center;
    font-family: 'Inter', sans-serif;
    font-weight: 600;
    font-size: 1rem;
    background: #F1F7FD;
}

.nav {
    display: flex;
    text-align: left;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    background: transparent;
    width: 100%;
}

.nav-items {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
}

.nav-items a {
    text-decoration: none;
    margin: 8px 24px;
    color: #2D323B;
}

.nav-items button {
    padding: 12px 30px;
    background-color: #41C2D2;
    color: #fff;
    border: none;
    box-shadow: 0px 4px 0px #31919D;
    border-radius: 8px;
    font-size: 1rem;
    font-weight: 600;
    font-family: 'Inter', sans-serif;
}

.nav-items button img {
    width: 12px;
    margin-left: 8px;
}
